$breakpoint-tablet: 992px;
$breakpoint-phone: 768px;

.gro-landing-cover {
    background-image: url('../img/gro-landing.png');
    height: 80vh;
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
}

.bg-blue-grad {
    background-image: linear-gradient(45deg, #0099FF 0%, #2b4fa7 100%);
    
}

@media (max-width: $breakpoint-tablet) {
    .gro-landing-cover {
        background-image: none;
        background-color: #BCCEFF !important;
    }
}

.gro-logo {
    width: 220px;
}

.gro-logo-white {
    width: 160px;
}

#features .icon {
    height: 5rem;
}

// Nav link
.nav-link.h5 {
    font-size: 1rem;
}

.gro-social.list-social-links a {
    color: $blue !important;
    // background-color: transparent;
}

.gro-footer {
    .nav-link {
        color: $white;
        &:hover {
            color: $blue;
        }
    }
}

.gro-grey-card {

}

.pl-cards {
    // height: 500px;
}

.ct-quotes {
    padding: 30px;
    background-color: rgba(0,0,0,0.5);
    border-radius: 5px;
}


// Timeline
.gro-b-border {
    height: 1px;
    // background-color: linear-gradient(90deg, #0099FF 0%, #6610f2 100%);
    background-image: linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,1), rgba(255,255,255,1), rgba(255,255,255,0)) !important;
    width: 100%;
    position: absolute;
    bottom: 24px;
    z-index: -10;
}
.gro-relative {
    position: relative;
}

canvas{ display: block; vertical-align: bottom; }
/* ---- particles.js container ---- */
#particles-js{ position:absolute; width: 100%; height: 100%; background-color: transparent; background-image: url(""); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
/* ---- stats.js ---- */
.count-particles{ background: #000022; position: absolute; top: 48px; left: 0; width: 80px; color: #13E8E9; font-size: .8em; text-align: left; text-indent: 4px; line-height: 14px; padding-bottom: 2px; font-family: Helvetica, Arial, sans-serif; font-weight: bold; }
.js-count-particles{ font-size: 1.1em; }
#stats, .count-particles{ -webkit-user-select: none; margin-top: 5px; margin-left: 5px; }
#stats{ border-radius: 3px 3px 0 0; overflow: hidden; }
.count-particles{ border-radius: 0 0 3px 3px; }